import React, { FC, ReactElement } from "react";

type CardProps = {
  title: string;
  remain: number;
  total: number;
};

const Card: FC<CardProps> = ({ title, remain, total }): ReactElement => {
  return (
    <>
      <div
        className={` ${
          remain === 0
            ? "bg-green-900"
            : remain < 5
            ? "bg-yellow-900"
            : "bg-primary-800"
        }  rounded text-center mr-2 overflow-hidden w-52`}
      >
        <div className="p-2 pb-1">
          <h4 className="text-white text-lg font-bold m-0">{title}</h4>
          <div className="flex text-center">
            <div className="-mt-0.5 px-2 border-r flex-1">
              <label className="text-sm text-gray-400">Distributed:</label>
              <h1 className="text-xl -mt-2 font-bold text-white">
                {total - remain}
              </h1>
            </div>
            <div className="-mt-0.5 px-2 flex-1">
              <label className="text-sm text-gray-400">Remaining:</label>
              <h1 className="text-xl -mt-2 font-bold text-white">{remain}</h1>
            </div>
          </div>
        </div>

        <div className="bg-blue-300  text-black px-2 py-1">
          <label className="text-sm">
            Total: <b>{total}</b>
          </label>
        </div>
      </div>
    </>
  );
};
export default Card;
