import React, { Fragment } from "react";
import { MdPushPin } from "react-icons/md";
import { connect } from "react-redux";
import {
  AcademicDDEDistribution,
  Auth,
  Basic,
  FC_GetDistributionSchools,
  FC_SaveDistributionSchool,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import CountDownCard from "../../components/CountDownCard/CountDownCard";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import InputSelect from "../../components/Inputs/InputSelect";
// import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import TableEditModal from "../../components/TableEditModal/TableEditModal";
import { StoreState } from "../../reducers";
import { RenderPageAccess, UsersAccess } from "../../utils/access";
import { search, shortString } from "../../utils/functions";
interface RemainPostsByQualification {
  qualification_id: string;
  qualification_title: string;
  total: number;
  remain: number;
}

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
}

interface AppState {
  loading: boolean;
  error: {
    target:
      | "requests-error"
      | "loading-update-error"
      | "main"
      | "main-success"
      | "update-error"
      | null;
    msg: string;
  };
  requests: AcademicDDEDistribution[];
  remain_post_by_levels: RemainPostsByQualification[];
  loading_requests: boolean;
  selected_level: string;
  selected_request_id: string;
  selected_value: string;
  selected_comment: string;
  selected_auto_level: string;
  search: string;
  pinned_school: { school_code: string; school_name: string } | null;
}

class _DistrictPostDistribution extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      requests: [],
      remain_post_by_levels: [],
      error: {
        target: null,
        msg: "",
      },
      loading_requests: false,
      selected_level: "",
      selected_request_id: "",
      selected_value: "0",
      selected_comment: "",
      selected_auto_level: "",
      search: "",
      pinned_school: null,
    };
  }

  submitLogin = (e: any) => {
    e.preventDefault();
  };

  selectRequest = (request: AcademicDDEDistribution) => {
    if (this.state.selected_request_id === "") {
      this.setState({
        selected_comment: request.dde_distribution_comment,
        selected_request_id: request.post_request_id,
        selected_value: request.dde_post_distribution + "",
      });
    }
  };

  cancelRequest = () =>
    this.setState({
      selected_comment: "",
      selected_request_id: "",
      selected_value: "0",
      error: {
        target: null,
        msg: "",
      },
    });

  cleanAlert() {
    setTimeout(() => {
      this.setState({
        error: {
          target: null,
          msg: "",
        },
      });
    }, 2000);
  }

  saveRequest = (request: AcademicDDEDistribution): void => {
    if (this.props.basic.academic_calender === null) return;
    let academic_id = this.props.basic.academic_calender.academic_year_id;

    this.setState({
      loading: true,
      error: {
        target: null,
        msg: "",
      },
    });
    FC_SaveDistributionSchool(
      {
        academic_year_id: academic_id,
        dde_distribution_comment: this.state.selected_comment,
        district_code: request.district_code,
        dde_post_distribution: +this.state.selected_value,
        position_code: request.position_code,
        post_request_id: request.post_request_id,
        qualification_id: request.qualification_id,
      },
      (status, data, msg) => {
        console.log({ status, data, msg });

        if (status === true && data !== null) {
          if (data.academic_distributed !== null) {
            this.cleanAlert();
            this.setState({
              loading: false,
              error: {
                target: "main-success",
                msg: "Post updated successful",
              },
              remain_post_by_levels: this.state.remain_post_by_levels.map(
                (itm) => {
                  let findData = data.total_distributed.find(
                    (el) => el.qualification_id === itm.qualification_id
                  );
                  return {
                    ...itm,
                    remain:
                      findData === undefined ? 0 : +findData.remaining_limits,
                  };
                }
              ),
              requests:
                data.academic_distributed === null
                  ? this.state.requests
                  : this.state.requests.map((req) =>
                      req.post_request_id ===
                      data.academic_distributed?.post_request_id
                        ? {
                            ...req,
                            dde_post_distribution:
                              +data.academic_distributed.dde_post_distribution,
                            dde_distribution_comment:
                              data.academic_distributed
                                .dde_distribution_comment,
                          }
                        : req
                    ),
              selected_comment: "",
              selected_value: "",
              selected_request_id: "",
            });
          } else {
            this.setState({
              loading: false,
              error: {
                target: "update-error",
                msg: data.message,
              },
            });
          }
        } else {
          this.setState({
            loading: false,
            error: {
              target: "update-error",
              msg: msg,
            },
          });
        }
      }
    );
  };

  componentDidMount = () => {
    RenderPageAccess(
      this.props.auth.role?.access,
      UsersAccess.DDE_DISTRIBUTION,
      () => {
        this.startApp();
      }
    );
  };

  startApp = () => {
    if (this.props.basic.academic_calender === null) {
      this.setState({
        error: {
          target: "main",
          msg: "failed to load data, please reload the page (Refresh)",
        },
      });
    } else if (this.props.auth.district === null) {
      this.setState({
        error: {
          target: "main",
          msg: "No district assigned to you",
        },
      });
    } else {
      let academic_id = this.props.basic.academic_calender.academic_year_id;
      let district_id = this.props.auth.district.district_code;
      // console.log({ academic_id: academic_id, district_id: district_id });

      this.setState({
        loading_requests: true,
      });

      //* Request
      FC_GetDistributionSchools(
        { academic_id: academic_id, district_id: district_id },
        (status, data, msg) => {
          if (status === true && data !== null) {
            console.log({ status, data, msg });

            this.setState({
              loading_requests: false,
              requests: data.academic_distributed.map((itm) => ({
                ...itm,
                limits: +itm.dde_post_distribution,
                dde_distribution_comment:
                  itm.dde_distribution_comment === null
                    ? ""
                    : itm.dde_distribution_comment,
              })),
              remain_post_by_levels: this.props.basic.qualifications.map(
                (qual) => {
                  let FindRemail = data.total_distributed.find(
                    (itm) => itm.qualification_id === qual.qualification_id
                  );
                  return {
                    qualification_id: qual.qualification_id,
                    qualification_title: qual.qualification_name,
                    remain:
                      FindRemail === undefined
                        ? 0
                        : +FindRemail.remaining_limits,
                    total: FindRemail === undefined ? 0 : +FindRemail.limits,
                  } as RemainPostsByQualification;
                }
              ),
            });
          } else {
            this.setState({
              loading_requests: false,
              error: {
                msg: msg,
                target: "requests-error",
              },
            });
          }
        }
      );
    }
  };

  pinSchool(code: string, name: string) {
    this.setState({ pinned_school: { school_code: code, school_name: name } });
  }

  render() {
    if (this.state.loading_requests === true) {
      return (
        <DashboardContainer>
          <div className="py-4 flex items-center justify-center">
            <Loading />
          </div>
        </DashboardContainer>
      );
    }

    let RequestsList =
      this.state.selected_level === ""
        ? this.state.requests
        : this.state.requests.filter(
            (itm) => itm.qualification_id === this.state.selected_level
          );

    RequestsList = search(RequestsList, this.state.search, {
      district_name: true,
    });

    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.error.target === "main" && (
          <Alert theme="danger">{this.state.error.msg}</Alert>
        )}
        {this.state.error.target === "loading-update-error" && (
          <Alert theme="danger">{this.state.error.msg}</Alert>
        )}

        <div className="sticky top-1 z-50 mx-auto">
          <DashboardContainer>
            <div className="flex mt-2 mx-auto">
              <div className="flex-1">
                <Heading>{this.props.auth.district?.district_name}</Heading>
                <Heading size="md">district distribution</Heading>
                <div className="w-44">
                  <InputSelect
                    data={[
                      { title: "All qualifications", value: "" },
                      ...this.props.basic.qualifications.map((el) => ({
                        title: el.qualification_name,
                        value: el.qualification_id,
                      })),
                    ]}
                    error=""
                    name="select_level"
                    onChange={(e) =>
                      this.setState({ selected_level: e.target.value })
                    }
                    label="Qualification"
                    placeholder="Select"
                    value={this.state.selected_level}
                    size="sm"
                  />
                </div>
              </div>
              <div className="">
                {/* <p className="opacity-60">Distribution</p> */}
                <div className="flex overflow-x-hidden">
                  {this.state.remain_post_by_levels.map((level, i) => {
                    if (i >= 5) return null;
                    return (
                      <CountDownCard
                        key={i}
                        remain={level.remain}
                        title={level.qualification_title}
                        total={level.total}
                      />
                    );
                  })}
                </div>
              </div>
            </div>
          </DashboardContainer>
        </div>

        <DashboardContainer>
          <div className="mt-3">
            <div className="border border-t mt-2 mb-1 flex items-center px-3 rounded">
              {/* <div className="flex-1 pr-2">
                <TextInput
                  error=""
                  label="Search"
                  name=""
                  onChange={(e) => this.setState({ search: e.target.value })}
                  placeholder="Search by district name"
                  value={this.state.search}
                  size="sm"
                />
              </div> */}
              <div className=""></div>
            </div>
            <table className="w-full text-left table-auto">
              <thead>
                <tr>
                  <th className="p-1 border">#</th>
                  <th className="p-1 border">School name</th>
                  <th className="p-1 border">Post name</th>
                  <th className="p-1 border">Qualification</th>
                  <th className="p-1 border">
                    Existing post{"("}s{")"}
                  </th>
                  {/* <th className="p-1 border ">School Post Requests</th> */}
                  {/* <th className="p-1 border">DDE post requests</th> */}
                  <th className="p-1 border text-green-800">New Post{"(s)"}</th>
                  <th className="p-1 border">Total posts</th>
                  <th className="p-1 border">Comment</th>
                </tr>
              </thead>
              <tbody>
                {RequestsList.length <= 0 ? (
                  <tr>
                    <td
                      colSpan={6}
                      className="p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                    >
                      <Heading size="md">No results found</Heading>
                    </td>
                  </tr>
                ) : (
                  RequestsList.map((req, i) => {
                    let Qualification = this.props.basic.qualifications.find(
                      (itm) => itm.qualification_id === req.qualification_id
                    );

                    if (
                      this.state.pinned_school !== null &&
                      this.state.pinned_school.school_code !== req.school_code
                    )
                      return null;
                    return (
                      <Fragment key={i}>
                        <tr className="border rounded hover:bg-blue-500 hover:bg-opacity-20">
                          <td className="p-2 border" key={i}>
                            {i + 1}
                          </td>
                          <td className="p-2 border font-bold">
                            {this.state.pinned_school === null && (
                              <button
                                onClick={this.pinSchool.bind(
                                  this,
                                  req.school_code,
                                  req.school_name
                                )}
                                className="p-1 bg-blue-200 rounded hover:text-blue-200 hover:bg-blue-700 text-blue-800"
                              >
                                <MdPushPin />
                              </button>
                            )}{" "}
                            {req.school_name}
                          </td>
                          <td className="p-2 border">{req.position_name}</td>
                          <td className="p-2 border">
                            {Qualification?.qualification_name}
                          </td>
                          <td className="p-2 border">{req.existing_post}</td>
                          {/* <td className="p-2 border  text-black">
                            {req.head_teacher_post_request}
                          </td> */}
                          {/* <td className="p-2 border">{req.dde_post_request}</td> */}

                          {this.state.selected_request_id ===
                          req.post_request_id ? (
                            <>
                              <TableEditModal
                                error={
                                  this.state.error.target === "update-error"
                                    ? this.state.error.msg
                                    : ""
                                }
                                comment={this.state.selected_comment}
                                onChangeComment={(e) =>
                                  this.setState({
                                    selected_comment: e.target.value,
                                  })
                                }
                                loading={this.state.loading}
                                onCancel={this.cancelRequest.bind(this)}
                                onChange={(e) =>
                                  this.setState({
                                    selected_value: e.target.value,
                                  })
                                }
                                onSave={this.saveRequest.bind(this, req)}
                                placeholderComment="Add comment"
                                value={this.state.selected_value}
                                minValue={"0"}
                              />
                            </>
                          ) : (
                            <td
                              className="p-2 border w-20 font-bold bg-green-100 bg-opacity-40 text-green-800 cursor-pointer"
                              onClick={this.selectRequest.bind(this, req)}
                            >
                              {req.dde_post_distribution}
                            </td>
                          )}
                          <td className="p-2 border w-20 font-bold">
                            {+req.dde_post_distribution + +req.existing_post}
                          </td>

                          <td
                            className="p-2 border"
                            title={req.dde_distribution_comment}
                          >
                            {shortString(req.dde_distribution_comment, 20)}
                          </td>
                        </tr>
                      </Fragment>
                    );
                  })
                )}
              </tbody>
            </table>
            <br />
            <br />
            <br />
            <br />
          </div>
          {this.state.pinned_school !== null && (
            <div className="flex p-2 z-50 left-60 border-t items-center bg-white fixed bottom-0 right-0 animate__animated animate__fadeInUp animate__faster">
              <div className="flex-1">
                <label htmlFor="" className="text-sm mb-0 text-gray-500">
                  Selected school name
                </label>
                <h2 className="font-bold text-lg">
                  {this.state.pinned_school.school_code}{" "}
                  {this.state.pinned_school.school_name}
                </h2>
              </div>

              <div className="">
                <Button
                  theme="danger-light"
                  onClick={() => this.setState({ pinned_school: null })}
                >
                  Unpin school
                </Button>
              </div>
            </div>
          )}
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const DistrictPostDistribution = connect(
  mapStateToProps,
  {}
)(_DistrictPostDistribution);
